<template>
  <div class="find-wrapper main-wrapper">
    <!--{{name}}-->
    <div style="width:100%;display: flex;">

      <a :class="{'find-btn':true,'find-nav-selected':selectedNav=='findkey'}" @click="goto('findkey')">findKey</a>
      <a :class="{'find-btn':true,'find-nav-selected':selectedNav=='findimg'}" @click="goto('findimg')">findimg</a>
    </div>
    <!--<router-link to="findKey">Go to Foo</router-link>-->
    <!--<router-link to="findImg">Go to Bar</router-link>-->
    <router-view style='position: absolute;flex:auto;'></router-view>
  </div>
</template>
<script>
  export default {
    name:'find',
    data(){
      return {
        name:'find.vue',
        selectedNav:'findkey'
      }
    },
    methods:{
        goto(name){
            this.$router.push({name:name});
            this.selectedNav = name;
        }
    },
    mounted(){
        this.goto('findkey');
    }

  }
</script>
<style>

  .find-btn{
    display: inline-flex;
    width:50%;
    height:1.5rem;
    background:#fff;
    border:0;
    color:black;
    justify-content:center;
    align-items:center;
  }
  .find-nav-selected{
    background:#ffc107;
    color:white;
  }
  .find-btn:visited,.find-btn:hover{
    text-decoration: none;
  }
</style>



