<template>
  <div class="wm-wrapper main-wrapper">
    <swiper v-once :options="swiperOption1" ref="mySwiperA1" class="wm-backImg">
      <swiper-slide v-for="(image,key) in images" :key="'swiper1'+key" style="overflow: hidden;">
        <img :src="image" style="height: 100%;min-width:100%;"/>
      </swiper-slide>
    </swiper>

    <!--<img v-lazy="backImg" class="wm-backImg"/>-->
    <div :class="{'desc-hide':true,'desc-show':descShow}"
         @click.stop="toggleDesc">
      {{theDesc}}
    </div>
    <swiper :options="swiperOption" class="my-swiper-container" ref="mySwiperA" v-once>
      <!-- 幻灯内容 -->
      <swiper-slide class="my-swiper-slide" v-for="(image,key) in images" :key="'swiper'+key">
        <div class="my-swipe-item">
          <img class="my-swipe-item-img" v-lazy="image" @click="toggleDesc(key)"/>
        </div>
      </swiper-slide>
    </swiper>

    <div class="wm-btn-wrapper">
      <mt-button class="wm-sure-btn" type="primary" @click.native="toggleDesc(-1)">查看</mt-button>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'waimai',
    data() {
      var self = this;
      return {
        images: [
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1489152661354&di=e41cd82998c0fdcad3c99d1121e88e60&imgtype=0&src=http%3A%2F%2Fpic17.nipic.com%2F20111122%2F6759425_152002413138_2.jpg',
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1489152661354&di=9a1100bb4c6886022e1e6d68574f0f9d&imgtype=0&src=http%3A%2F%2Fimg.tuku.cn%2Ffile_big%2F201502%2F0e93d8ab02314174a933b5f00438d357.jpg',
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1489152661353&di=441d7a136dc5ba611372574a3d998fe8&imgtype=0&src=http%3A%2F%2Fpic1.win4000.com%2Fwallpaper%2Fc%2F57849d650ef11.jpg',
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1489152661353&di=d8ab6f559a13b633a6d9a4b790244a8b&imgtype=0&src=http%3A%2F%2Ftupian.enterdesk.com%2F2013%2Fmxy%2F12%2F11%2F4%2F3.jpg',
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1489152661353&di=6dd2b573dc865cb86d5f0ba62a6de52a&imgtype=0&src=http%3A%2F%2Fwww.bz55.com%2Fuploads%2Fallimg%2F150604%2F139-150604162F5.jpg',
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1489152661353&di=9af90dea1b094d5f88cc92140ad04a48&imgtype=0&src=http%3A%2F%2Ftupian.enterdesk.com%2F2013%2Fmxy%2F12%2F11%2F4%2F4.jpg'
        ],
        descShow: false,
//        backImg: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1489152661354&di=e41cd82998c0fdcad3c99d1121e88e60&imgtype=0&src=http%3A%2F%2Fpic17.nipic.com%2F20111122%2F6759425_152002413138_2.jpg',
        theSwipeIndex: 0,
        theDesc: '',
        desc: [
          '项目1的说明2333',
          '项目2的说明2333',
          '项目3的说明2333',
          '项目4的说明2333',
          '项目5的说明2333',
          '项目6的说明2333',
        ],
        swiperOption1: {
          slidesPerView: "auto",
          centeredSlides: !0,
          watchSlidesProgress: !0,
          pagination: ".swiper-pagination",
          paginationClickable: !0,
          debugger: true
        },
        swiperOption: {
          slidesPerView: "auto",
          centeredSlides: !0,
          watchSlidesProgress: !0,
          pagination: ".swiper-pagination",
          paginationClickable: !0,
          debugger: true,
          onTransitionStart(swiper) {
//                    self.backImg = self.images[swiper.activeIndex];
            self.theSwipeIndex = swiper.activeIndex;
          }
        }
      }
    },
    computed: {
      swiperA() {
        return this.$refs.mySwiperA.swiper
      },
      swiperA1() {
        return this.$refs.mySwiperA1.swiper
      },
    },
    methods: {
      handleClick: () => {
        console.log('点击触发 handleClick');
      },
      toggleDesc(index){
        this.descShow = !this.descShow;
        var index1 = index > -1 ? index : this.theSwipeIndex;
        this.theDesc = this.desc[index1];
      }

    },
    mounted() {
      setTimeout(() => {
        this.swiperA.params.control = this.swiperA1;
      }, 100)
      var wmWrapper = document.querySelector('.wm-wrapper')[0];
    }
  }
</script>
<style>
  .wm-wrapper {
    position: static !important;
    width: 100%;
    flex: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;
  }

  .wm-backImg {
    position: absolute !important;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: -100 !important;
    background: rgba(0, 0, 0, .5);
    filter: blur(0.27rem);
    background-repeat: no-repeat !important;
    -webkit-background-size: cover !important;
    background-size: cover !important;
    transition: all .5s;
  }

  .my-swiper-container {
    width: 100%;
    flex: auto;

    position: static !important;
    display: flex;
    align-items: center;
  }

  .my-swiper-container > .swiper-wrapper {
    width: 100%;
    height: 7.5rem !important;
    position: static !important;
  }

  .my-swiper-slide {
    width: 6rem !important;
    height: 100%;
    display: flex !important;
    justify-content: center !important;
    /*padding:1rem;*/
  }

  .my-swipe-item {
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ccc;
    border-radius: .3rem;
    box-shadow: 0 0 0.07rem #ccc;
    /*padding: 1rem;*/
    overflow: hidden;
  }

  .wm-btn-wrapper {
    /*height: 1.5rem;*/
    display: flex;
    flex: none;
    padding-bottom: 1.5rem;
    justify-content: center;
    align-items: center;
  }

  .wm-sure-btn {
    height: 1rem !important;
    border-radius: 0.5em !important;
    width: 3rem;
  }

  image[lazy=loading] {
    width: 100%;
  }

  .my-swipe-item-img {
    height: 100%;
  }

  .desc-hide {
    position: fixed;
    width: 100%;
    height: 0;
    transition: all .5s;
    z-index: -1;
    overflow: auto;
    background: rgba(0, 0, 0, 0);
    font-size: .5rem !important;
    transform: transition3d(0, 0, 0);
  }

  .desc-show {
    /*position:fixed;*/
    width: 100vw;
    height: 100vh;
    background: black;
    opacity: .6;
    color: white;
    padding: 1rem;
    left: 0;
    top: 0;
    transition: all .5s;
    z-index: 100;
  }
</style>



