import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'

import waimai from '../components/menu1/waimai.vue';
import order from '../components/menu2/order.vue';
import find from '../components/menu3/find.vue';
import mine from '../components/menu4/mine.vue';
import findkey from '../components/menu3/components/findKey.vue';
import findimg from '../components/menu3/components/findImg.vue';


Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else if(to.hash){
      return {selector:to.hash}
    } else {
      return {x: 0, y: 0}
    }
  },
  routes: [
    {
      path: '/',
      name: 'waimai',
      component: waimai
    }, {
      path: '/wm',
      name: 'waimai',
      component: waimai,
    }, {
      path: '/order',
      name: 'order',
      component: order,
    }, {
      path: '/find',
      name: 'find',
      component: find,
      children: [{
        name: 'findkey',
        path: '',
        component: findkey,
        meta: { requiresAuth: true }
      },{
        name: 'findimg',
        path: 'findimg',
        component: findimg,
        meta: { requiresAuth: true }
      }]
    }, {
      path: '/mine',
      name: 'mine',
      component: mine,
    }
  ]
})



